import { useCallback, useMemo, useState } from 'react'

/**
 * Ð”Ð¾Ð±Ð°Ð²Ð»ÑÐµÑ‚ ÐºÐ¾Ð¼Ð¿Ð¾Ð½ÐµÐ½Ñ‚Ñƒ Ñ„ÑƒÐ½ÐºÑ†Ð¸Ð¾Ð½Ð°Ð»ÑŒÐ½Ð¾ÑÑ‚ÑŒ Ð¿Ð¾ ÑƒÐ¿Ñ€Ð°Ð²Ð»ÐµÐ½Ð¸ÑŽ Ð¾Ñ‚Ð¾Ð±Ñ€Ð°Ð¶ÐµÐ½Ð¸ÐµÐ¼ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ ÐºÐ¾Ð½Ñ‚ÐµÐ½Ñ‚Ð° (Ð¿Ð¾Ð¿Ð°Ð¿Ñ‹, Ð´Ñ€Ð¾Ð¿Ð´Ð°ÑƒÐ½Ñ‹ Ð¸ Ñ‚.Ð¿.).
 * @param defaultVisible - Ð½Ð°Ñ‡Ð°Ð»ÑŒÐ½Ð¾Ðµ ÑÐ¾ÑÑ‚Ð¾ÑÐ½Ð¸Ðµ Ð²Ð¸Ð´Ð¸Ð¼Ð¾ÑÑ‚Ð¸ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ ÐºÐ¾Ð½Ñ‚ÐµÐ½Ñ‚Ð°
 * @returns - { visible, show, hide, toggle }
 *
 *  - visible - Ñ‚ÐµÐºÑƒÑ‰ÐµÐµ ÑÐ¾ÑÑ‚Ð¾ÑÐ½Ð¸Ðµ Ð²Ð¸Ð´Ð¸Ð¼Ð¾ÑÑ‚Ð¸ ÐºÐ¾Ð½Ñ‚ÐµÐ½Ñ‚Ð°
 *  - show - Ð¿ÐµÑ€ÐµÐºÐ»ÑŽÑ‡Ð°ÐµÑ‚ ÐºÐ¾Ð½Ñ‚ÐµÐ½Ñ‚ Ð² Ð²Ð¸Ð´Ð¸Ð¼Ð¾Ðµ ÑÐ¾ÑÑ‚Ð¾ÑÐ½Ð¸Ðµ
 *  - hide - Ð¿ÐµÑ€ÐµÐºÐ»ÑŽÑ‡Ð°ÐµÑ‚ ÐºÐ¾Ð½Ñ‚ÐµÐ½Ñ‚ Ð² ÑÐºÑ€Ñ‹Ñ‚Ð¾Ðµ ÑÐ¾ÑÑ‚Ð¾ÑÐ½Ð¸Ðµ
 *  - toggle - Ð¿ÐµÑ€ÐµÐºÐ»ÑŽÑ‡Ð°ÐµÑ‚ Ð²Ð¸Ð´Ð¸Ð¼Ð¾ÑÑ‚ÑŒ ÐºÐ¾Ð½Ñ‚ÐµÐ½Ñ‚Ð° Ð² Ð¿Ñ€Ð¾Ñ‚Ð¸Ð²Ð¾Ð¿Ð¾Ð»Ð¾Ð¶Ð½Ð¾Ðµ ÑÐ¾ÑÑ‚Ð¾ÑÐ½Ð¸Ðµ
 */
const useModal = (defaultVisible = false) => {
  const [visible, setVisible] = useState(defaultVisible)

  const show = useCallback(() => setVisible(true), [])

  const hide = useCallback(() => setVisible(false), [])

  const toggle = useCallback(() => setVisible((prevValue) => !prevValue), [])

  return useMemo(
    () => ({
      show,
      hide,
      toggle,
      visible,
    }),
    [hide, show, toggle, visible]
  )
}

export { useModal }
