import { ServerErrors, ProcessedError, ServerError } from './types';
import { ERROR_MESSAGES } from './constants';

/**
 * ÐžÑÐ½Ð¾Ð²Ð½Ð¾Ð¹ Ð¾Ð±Ñ€Ð°Ð±Ð¾Ñ‚Ñ‡Ð¸Ðº Ð¾ÑˆÐ¸Ð±Ð¾Ðº ÑÐµÑ€Ð²ÐµÑ€Ð°
 */
const handleServerErrors = (errors: ServerErrors): ProcessedError[] => {
  return errors.errors.map(processSingleError);
};

/**
 * ÐžÐ±Ñ€Ð°Ð±Ð°Ñ‚Ñ‹Ð²Ð°ÐµÑ‚ Ð¾Ð´Ð½Ñƒ Ð¾ÑˆÐ¸Ð±ÐºÑƒ ÑÐµÑ€Ð²ÐµÑ€Ð°
 */
const processSingleError = (error: ServerError): ProcessedError => {
  return {
    code: error.extensions.code,
    message: getErrorMessage(error),
    field: error.extensions.fieldName,
    original: error,
  };
};

/**
 * ÐŸÐ¾Ð»ÑƒÑ‡Ð°ÐµÑ‚ ÑÐ¾Ð¾Ð±Ñ‰ÐµÐ½Ð¸Ðµ Ð¾Ð± Ð¾ÑˆÐ¸Ð±ÐºÐµ Ð² Ð¿Ð¾Ñ€ÑÐ´ÐºÐµ Ð¿Ñ€Ð¸Ð¾Ñ€Ð¸Ñ‚ÐµÑ‚Ð°:
 * 1. Ð¡Ð¾Ð¾Ð±Ñ‰ÐµÐ½Ð¸Ðµ Ð¸Ð· ERROR_MESSAGES
 * 2. Ð¡Ñ‚Ð°Ð½Ð´Ð°Ñ€Ñ‚Ð½Ð¾Ðµ message Ð¸Ð· Ð¾ÑˆÐ¸Ð±ÐºÐ¸
 * 3. Ð—Ð°Ð¿Ð°ÑÐ½Ð¾Ð¹ Ð²Ð°Ñ€Ð¸Ð°Ð½Ñ‚ "ÐÐµÐ¸Ð·Ð²ÐµÑÑ‚Ð½Ð°Ñ Ð¾ÑˆÐ¸Ð±ÐºÐ°"
 */
const getErrorMessage = (error: ServerError): string => {
  return ERROR_MESSAGES[error.extensions.code] || error.message || 'ÐÐµÐ¸Ð·Ð²ÐµÑÑ‚Ð½Ð°Ñ Ð¾ÑˆÐ¸Ð±ÐºÐ°';
};

/**
 * ÐžÐ±Ñ€Ð°Ð±Ð¾Ñ‚Ñ‡Ð¸Ðº Ð´Ð»Ñ react-hook-form
 */
const handleFormErrors = (
  errors: ServerErrors,
  setError: (field: string, error: { type: string; message: string }) => void,
): void => {
  handleServerErrors(errors).forEach(({ field, message }) => {
    if (field) {
      setError(field, {
        type: 'server',
        message: message,
      });
    }
  });
};

/**
 * Ð’Ð¾Ð·Ð²Ñ€Ð°Ñ‰Ð°ÐµÑ‚ Ð¿ÐµÑ€Ð²ÑƒÑŽ Ð¾ÑˆÐ¸Ð±ÐºÑƒ Ð´Ð»Ñ Ð¿Ð¾ÐºÐ°Ð·Ð° Ð² UI
 */
const getFirstError = (errors: ServerErrors): string => {
  if (!errors?.errors || errors.errors.length === 0) {
    return 'ÐÐµÐ¸Ð·Ð²ÐµÑÑ‚Ð½Ð°Ñ Ð¾ÑˆÐ¸Ð±ÐºÐ°';
  }

  const firstError = errors.errors[0];
  return ERROR_MESSAGES[firstError.extensions.code] || firstError.message || 'ÐÐµÐ¸Ð·Ð²ÐµÑÑ‚Ð½Ð°Ñ Ð¾ÑˆÐ¸Ð±ÐºÐ°';
};

export { handleServerErrors, processSingleError, getErrorMessage, handleFormErrors, getFirstError };
