import { type SubmitHandler, useForm } from 'react-hook-form';
import { Link } from 'react-router-dom';
import { useMutation } from '@apollo/client';
import { LOGIN } from '@/graphql';
import { useAuth } from '@/stores/useAuth';
import { Button, Input } from '@/ui';
import { type FormLoginData } from './FormLogin.types';
import { useApolloErrorHandler } from '@/apollo/useApolloErrorHandler';
import { useToast } from '@/serviÑes/ToastContext';
import styles from './FormLogin.module.css';

const FormLogin = () => {
  const { show } = useToast();
  const { handleQueryErrors } = useApolloErrorHandler();

  const {
    formState: { errors, isValid },
    handleSubmit,
    register,
  } = useForm<FormLoginData>({ mode: 'onChange' });
  const { login } = useAuth();

  const [_login, { loading }] = useMutation(LOGIN, {
    onCompleted: (data) => {
      login(data.profile.signin.token);
    },
    onError: (error) => {
      const processedErrors = handleQueryErrors(error);
      show(processedErrors, 'error');
    },
  });

  const onSubmit: SubmitHandler<FormLoginData> = async (data) => {
    try {
      await _login({
        variables: {
          email: data.email,
          password: data.password,
        },
      });
    } catch (error) {
      const processedErrors = handleQueryErrors(error);
      show(processedErrors, 'error');
    }
  };

  return (
    <div className={styles.loginContainer}>
      <h2 className={styles.title}>Ð’Ñ…Ð¾Ð´</h2>
      <form className={styles.form} data-testid="login-form" onSubmit={handleSubmit(onSubmit)}>
        <Input
          error={errors.email?.message}
          id="email"
          label="Email"
          type="email"
          {...register('email', {
            required: 'Email Ð¾Ð±ÑÐ·Ð°Ñ‚ÐµÐ»ÐµÐ½',
            pattern: {
              value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
              message: 'ÐÐµÐºÐ¾Ñ€Ñ€ÐµÐºÑ‚Ð½Ñ‹Ð¹ email',
            },
          })}
        />

        <Input
          error={errors.password?.message}
          id="password"
          label="ÐŸÐ°Ñ€Ð¾Ð»ÑŒ"
          type="password"
          {...register('password', {
            required: 'ÐŸÐ°Ñ€Ð¾Ð»ÑŒ Ð¾Ð±ÑÐ·Ð°Ñ‚ÐµÐ»ÐµÐ½',
          })}
        />

        <Link to="/registration">Ð—Ð°Ñ€ÐµÐ³Ð¸ÑÑ‚Ñ€Ð¸Ñ€Ð¾Ð²Ð°Ñ‚ÑŒÑÑ</Link>

        <Button
          color="Primary"
          disabled={!isValid || loading}
          text={loading ? 'Ð’Ñ…Ð¾Ð´...' : 'Ð’Ð¾Ð¹Ñ‚Ð¸'}
          type="submit"
        />
      </form>
    </div>
  );
};

export { FormLogin, type FormLoginData };
